@import url(https://fonts.googleapis.com/css2?family=Alfa+Slab+One&display=swap);
/* Document
 * ========================================================================== */

/**
 * 1. Correct the line height in all browsers.
 * 2. Prevent adjustments of font size after orientation changes in
 *    IE on Windows Phone and in iOS.
 */

html {
  line-height: 1.15; /* 1 */
  -ms-text-size-adjust: 100%; /* 2 */
  -webkit-text-size-adjust: 100%; /* 2 */
}

/* Sections
 * ========================================================================== */

/**
 * Correct the font size and margin on `h1` elements within `section` and
 * `article` contexts in Chrome, Edge, Firefox, and Safari.
 */

h1 {
  font-size: 2em;
  margin: 0.67em 0;
}

/* Grouping content
 * ========================================================================== */

/**
 * Remove the margin on nested lists in Chrome, Edge, IE, and Safari.
 */

dl dl,
dl ol,
dl ul,
ol dl,
ul dl {
  margin: 0;
}

/**
 * Remove the margin on nested lists in Edge 18- and IE.
 */

ol ol,
ol ul,
ul ol,
ul ul {
  margin: 0;
}

/**
 * 1. Add the correct box sizing in Firefox.
 * 2. Show the overflow in Edge 18- and IE.
 */

hr {
  box-sizing: content-box; /* 1 */
  height: 0; /* 1 */
  overflow: visible; /* 2 */
}

/**
 * Add the correct display in IE.
 */

main {
  display: block;
}

/**
 * 1. Correct the inheritance and scaling of font size in all browsers.
 * 2. Correct the odd `em` font sizing in all browsers.
 */

pre {
  font-family: monospace, monospace; /* 1 */
  font-size: 1em; /* 2 */
}

/* Text-level semantics
 * ========================================================================== */

/**
 * Add the correct text decoration in Edge 18-, IE, and Safari.
 */

abbr[title] {
  text-decoration: underline;
  text-decoration: underline dotted;
}

/**
 * Add the correct font weight in Chrome, Edge, and Safari.
 */

b,
strong {
  font-weight: bolder;
}

/**
 * 1. Correct the inheritance and scaling of font size in all browsers.
 * 2. Correct the odd `em` font sizing in all browsers.
 */

code,
kbd,
samp {
  font-family: monospace, monospace; /* 1 */
  font-size: 1em; /* 2 */
}

/**
 * Add the correct font size in all browsers.
 */

small {
  font-size: 80%;
}

/* Embedded content
 * ========================================================================== */

/**
 * Hide the overflow in IE.
 */

svg:not(:root) {
  overflow: hidden;
}

/* Forms
 * ========================================================================== */

/**
 * Remove the margin on controls in Safari.
 */

button,
input,
select {
  margin: 0;
}

/**
 * 1. Show the overflow in IE.
 * 2. Remove the inheritance of text transform in Edge 18-, Firefox, and IE.
 */

button {
  overflow: visible; /* 1 */
  text-transform: none; /* 2 */
}

/**
 * Correct the inability to style buttons in iOS and Safari.
 */

button,
[type="button"],
[type="reset"],
[type="submit"] {
  -webkit-appearance: button;
}

/**
 * Correct the padding in Firefox.
 */

fieldset {
  padding: 0.35em 0.75em 0.625em;
}

/**
 * Show the overflow in Edge 18- and IE.
 */

input {
  overflow: visible;
}

/**
 * 1. Correct the text wrapping in Edge 18- and IE.
 * 2. Correct the color inheritance from `fieldset` elements in IE.
 */

legend {
  box-sizing: border-box; /* 1 */
  color: inherit; /* 2 */
  display: table; /* 1 */
  max-width: 100%; /* 1 */
  white-space: normal; /* 1 */
}

/**
 * 1. Add the correct display in Edge 18- and IE.
 * 2. Add the correct vertical alignment in Chrome, Edge, and Firefox.
 */

progress {
  display: inline-block; /* 1 */
  vertical-align: baseline; /* 2 */
}

/**
 * Remove the inheritance of text transform in Firefox.
 */

select {
  text-transform: none;
}

/**
 * 1. Remove the margin in Firefox and Safari.
 * 2. Remove the default vertical scrollbar in IE.
 */

textarea {
  margin: 0; /* 1 */
  overflow: auto; /* 2 */
}

/**
 * 1. Correct the odd appearance in Chrome, Edge, and Safari.
 * 2. Correct the outline style in Safari.
 */

[type="search"] {
  -webkit-appearance: textfield; /* 1 */
  outline-offset: -2px; /* 2 */
}

/**
 * Correct the cursor style of increment and decrement buttons in Safari.
 */

::-webkit-inner-spin-button,
::-webkit-outer-spin-button {
  height: auto;
}

/**
 * Correct the text style of placeholders in Chrome, Edge, and Safari.
 */

::-webkit-input-placeholder {
  color: inherit;
  opacity: 0.54;
}

/**
 * Remove the inner padding in Chrome, Edge, and Safari on macOS.
 */

::-webkit-search-decoration {
  -webkit-appearance: none;
}

/**
 * 1. Correct the inability to style upload buttons in iOS and Safari.
 * 2. Change font properties to `inherit` in Safari.
 */

::-webkit-file-upload-button {
  -webkit-appearance: button; /* 1 */
  font: inherit; /* 2 */
}

/**
 * Remove the inner border and padding of focus outlines in Firefox.
 */

::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

/**
 * Restore the focus outline styles unset by the previous rule in Firefox.
 */

:-moz-focusring {
  outline: 1px dotted ButtonText;
}

/**
 * Remove the additional :invalid styles in Firefox.
 */

:-moz-ui-invalid {
  box-shadow: none;
}

/* Interactive
 * ========================================================================== */

/*
 * Add the correct display in Edge 18- and IE.
 */

details {
  display: block;
}

/*
 * Add the correct styles in Edge 18-, IE, and Safari.
 */

dialog {
  background-color: white;
  border: solid;
  color: black;
  display: block;
  height: -moz-fit-content;
  height: -webkit-fit-content;
  height: fit-content;
  left: 0;
  margin: auto;
  padding: 1em;
  position: absolute;
  right: 0;
  width: -moz-fit-content;
  width: -webkit-fit-content;
  width: fit-content;
}

dialog:not([open]) {
  display: none;
}

/*
 * Add the correct display in all browsers.
 */

summary {
  display: list-item;
}

/* Scripting
 * ========================================================================== */

/**
 * Add the correct display in IE.
 */

template {
  display: none;
}

/* User interaction
 * ========================================================================== */

.nav-bar {
  display: flex;
  justify-content: space-between;
  background-image: url(/shopping-cart/static/media/wood.dcf3d3ac.jpg);
  align-items: center;
}

.product-img {
  width: 15%;
  margin: 0.5rem;
}

.nav-item {
  display: inline;
  font-family: 'Alfa Slab One', cursive;
  font-size: 2rem;
  margin-right: 5.5rem;
}

.nav-item a {
  text-decoration: none;
  color: black;
}

.nav-item a:hover,
.shopping-cart:hover {
  color: red;
}

body {
  margin: 0;
  font-family: sans-serif;
}

.home-link {
  margin-right: 5.5rem;
}

.shop-link {
  margin-right: 5.5rem;
}

.nav-bar-link-list {
  margin-right: 2.5rem;
  margin-top: 0;
  margin-bottom: 0;
  display: flex;
}

.homepage-body {
  display: flex;
  align-items: center;
  flex: auto;
}

.homepage {
  background-image: url(/shopping-cart/static/media/textured-background.b28abe8a.jpg);
  height: 100vh;
  display: flex;
  flex-direction: column;
}

.shop-page {
  background-image: url(/shopping-cart/static/media/textured-background.b28abe8a.jpg);
}

.product-selection-wrapper {
  display: flex;
  justify-content: center;
}

.forrest-pic {
  width: 38%;
}

.about-buybtn-container {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.about {
  width: 80%;
  font-size: 1.5rem;
}

.buy-shrimps-btn {
  padding: 0.5rem;
  border: none;
  background: red;
  color: white;
  text-decoration: none;
  font-family: 'Alfa Slab One', cursive;
}

.buy-shrimps-btn:hover {
  background: blue;
}

.shrimp-wrapper {
  display: flex;
  flex-wrap: wrap;
  width: 80%;
}

.product {
  width: 25%;
  margin: 1rem;
  border: 1px solid black;
  background: white;
  border-radius: 4px;
  padding: 0.5rem;
}

.shrimp-image {
  width: 100%;
}

input[type="number"] {
  margin-bottom: 0.5rem;
  margin-left: auto;
  margin-right: auto;
  width: 80%;
  display: block;
}

.add-to-cart-btn {
  margin-left: auto;
  font-family: 'Alfa Slab One', cursive;
  margin-right: auto;
  width: 80%;
  display: block;
  background: red;
  color: white;
}

.add-to-cart-btn:hover {
  background: blue;
}

.shrimp-type {
  margin-top: 10px;
  margin-bottom: 10px;
  font-size: 1.3em;
}

.quote {
  margin-left: 1.5rem;
  margin-right: 1.5rem;
  font-style: italic;
}

.count {
  font-size: 1rem;
}

